@charset "UTF-8";
.guest-you-like {
  padding: 16px 20px 0; }
  .guest-you-like > h3 {
    height: 22px;
    font-size: 16px;
    line-height: 22px;
    margin: 0 0 12px;
    color: #333; }
  .guest-you-like li {
    font-size: 0; }
    .guest-you-like li > img {
      border-radius: 5px;
      width: 100%;
      height: 116px; }
    .guest-you-like li > .name {
      font-size: 14px;
      line-height: 20px;
      color: #222;
      margin: 10px 0 4px; }
    .guest-you-like li > .area {
      font-size: 12px;
      line-height: 17px;
      color: #999; }
    .guest-you-like li > .price {
      font-size: 22px;
      color: #f60;
      line-height: 27px; }
      .guest-you-like li > .price:before {
        content: "¥";
        font-size: 14px; }
