

































































































































































.produce-list {
    padding: 15px 20px;
    font-weight: 400;
    font-size: 14px;
    color: #666666;

    .active {
        color: #31BBAC;
    }

    .sort-option {
        span {
            cursor: pointer;
            position: relative;

            .arrow {
                color: #ccc;
                position: absolute;
                left: 100%;
                font-size: 5px;

                &.el-icon-caret-top {
                    top: 1px;
                }

                &.el-icon-caret-bottom {
                    bottom: -2px;
                }

                &.active {
                    color: #31BBAC;
                }
            }
        }
    }

    .produce-content {
        margin-top: 11px;
        background-color: #fff;

        .food-produce-wrapper {
            padding: 20px 0 16px;
            border-top: 1px solid #E5E5E5;;
        }
    }
}
