








































































































































.s-wrapper {
    padding-top: 10px;

    .nav {
        margin: 10px 0;
    }

    .link-style {
        .el-breadcrumb__inner {

            cursor: pointer;
            font-size: 12px;
            color: #222;
            font-weight: 400;

            &:hover {
                color: #13D1BE;
            }
        }

        .el-breadcrumb__separator {
            color: #222;
            font-size: 7px;
        }
    }

    .s-content {
        margin-top: 10px;

        .left-content {
            background: #FFF;
            border: 1px solid #E5E5E5;
            border-radius: 4px;
        }
    }

    .produce-wrapper {
        background-color: #fff;
        margin-top: 10px;
        border: 1px solid #E5E5E5;
        border-radius: 4px;
    }

    .g-y-l-w {
        background-color: #fff;
        border: 1px solid #e5e5e5;
        border-radius: 4px;
    }
}

