@charset "UTF-8";
.food-produce-card .ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.food-produce-card .main-img img {
  width: 220px;
  height: 125px;
  border-radius: 5px; }

.food-produce-card .main-img span {
  position: absolute;
  top: 2px;
  left: -3px;
  width: 20px;
  height: 13px;
  background-color: #FB6433;
  line-height: 13px;
  font-size: 12px;
  text-align: center;
  color: #fff; }

.food-produce-card .card-content {
  padding-left: 20px;
  padding-bottom: 10px; }
  .food-produce-card .card-content .content-desc {
    padding-bottom: 10px; }
    .food-produce-card .card-content .content-desc h3 {
      font-weight: 500;
      font-size: 16px;
      color: #333; }
    .food-produce-card .card-content .content-desc > .el-row {
      height: 20px; }
      .food-produce-card .card-content .content-desc > .el-row:not(:first-of-type) {
        margin-top: 6px; }
    .food-produce-card .card-content .content-desc .el-rate {
      display: inline-block; }
      .food-produce-card .card-content .content-desc .el-rate span {
        margin: 0; }
    .food-produce-card .card-content .content-desc span {
      margin-right: 10px;
      font-size: 12px;
      vertical-align: middle; }
      .food-produce-card .card-content .content-desc span.comment-sum {
        color: #F90; }
      .food-produce-card .card-content .content-desc span.cat-info {
        width: 104px;
        display: inline-block; }

.food-produce-card .deal-container {
  border-top: 1px dashed #E5E5E5;
  padding-top: 10px; }
  .food-produce-card .deal-container .deal {
    padding: 6px 10px 8px; }
    .food-produce-card .deal-container .deal h3 {
      font-size: 14px;
      color: #666;
      height: 20px;
      line-height: 20px; }
      .food-produce-card .deal-container .deal h3:hover {
        color: #31BBAC; }
    .food-produce-card .deal-container .deal span {
      margin-right: 10px;
      font-size: 12px;
      color: #999999; }
      .food-produce-card .deal-container .deal span.deal-price {
        color: #f04d4e;
        font-size: 15px; }
        .food-produce-card .deal-container .deal span.deal-price:before {
          content: "¥";
          font-size: 12px; }
    .food-produce-card .deal-container .deal:hover {
      background-color: #F8F8F8;
      border-radius: 4px; }
  .food-produce-card .deal-container .more-btn {
    margin-left: 10px; }
    .food-produce-card .deal-container .more-btn span {
      display: inline-block;
      margin-top: 16px;
      height: 12px;
      line-height: 12px;
      font-size: 12px;
      cursor: pointer; }
      .food-produce-card .deal-container .more-btn span:hover {
        color: #31BBAC; }
